.homePage {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.up {
    width: 100px;
    height: 50px;
    color: rgb(7, 81, 165);
    z-index: 9999999;
    position: fixed;
    top: 75%;
    right: 5px;
    display: none;
    cursor: pointer;
}

html {
    --scrollbarBG: rgb(30, 30, 31);
    --thumbBG: rgb(168, 2, 160);
}

body::-webkit-scrollbar {
    width: 10px;
}

body {
    scrollbar-width: thin;
    scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

body::-webkit-scrollbar-track {
    background: var(--scrollbarBG);
}

body::-webkit-scrollbar-thumb {
    background-color: var(--thumbBG);
    border-radius: 25px;
    border: 0px solid var(--scrollbarBG);
}

.App {
    text-align: center;
}

.background {
    z-index: -1;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.background img {
    min-width: 100%;
    max-height: 100%;
}

.header-bar {
    padding: 2px 10px;
    background-color: rgb(46, 145, 7);
    color: #fff;
    background: #00bd56;
    background: #207dff;
    background: -moz-linear-gradient(45deg, #207dff 0%, #00bd55 100%);
    background: -webkit-linear-gradient(45deg, #207dff 0%, #00bd55 100%);
    background: -o-linear-gradient(45deg, #207dff 0%, #00bd55 100%);
    background: -ms-linear-gradient(45deg, #207dff 0%, #00bd55 100%);
    background: linear-gradient(45deg, #207dff 0%, #00bd55 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#207dff', endColorstr='#00bd55', GradientType=1);
}

.header-bar .social-media i {
    margin-left: 10px;
    cursor: pointer;
    -webkit-transition: .5se ease;
    -moz-transition: .5se ease;
    -o-transition: .5se ease;
    transition: .5se ease;
}

.header-bar a {
    text-decoration: none;
    color: #333;
}

.header-bar .social-media i:hover {
    color: blueviolet;
}

.navbar {
    background: #f1f8f4;
    background: #9565b1;
    background: -moz-linear-gradient(45deg, #f1f8f4 0%, #9565b1 100%);
    background: -webkit-linear-gradient(45deg, #f1f8f4 0%, #9565b1 100%);
    background: -o-linear-gradient(45deg, #f1f8f4 0%, #9565b1 100%);
    background: -ms-linear-gradient(45deg, #f1f8f4 0%, #9565b1 100%);
    background: linear-gradient(45deg, #f1f8f4 0%, #9565b1 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#207dff', endColorstr='#00bd55', GradientType=1);
}

.navbar .nav-btn {
    border: none;
}

.navbar-light .navbar-nav .nav-link {
    color: rgb(255, 255, 255);
    font-size: large;
    font-weight: bold;
    margin-left: 40px;
    -webkit-transition: 1se;
    -moz-transition: 1se;
    -o-transition: 1se;
    transition: 1se;
    padding-left: 0;
    padding-right: 0;
    padding-top: .9rem;
    padding-bottom: .9rem;
    position: relative;
    text-transform: uppercase;
    opacity: 1!important;
}

.navbar-light .navbar-nav .nav-link:hover {
    color: green;
    border-bottom: 2px solid green;
}

.navbar-light .navbar-brand {
    text-shadow: 0 2px 2px rgb(73, 59, 2);
    font-weight: bolder;
    font-size: xx-large;
}

.navbar-brand {
    font-weight: bold;
    position: relative;
    left: 0%;
}

.navbar-nav .nav-link {
    margin-left: 20px;
    text-transform: uppercase;
    font-size: 16px;
    color: rgb(69, 112, 150);
}

.navbar-light .navbar-nav .active>.nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show>.nav-link {
    color: green;
    font-weight: 500;
    border-bottom: 2px solid green;
}

img.i {
    width: 100px;
    height: 100px;
}

.startable {
    padding-top: 100px;
    padding-bottom: 100px;
}

.startable h1 {
    font-size: 70px;
    color: rgb(46, 145, 7);
    font-weight: 900;
}

.startable button {
    font-size: x-large;
    font-weight: 900;
    -webkit-transition: .5se ease;
    -moz-transition: .5se ease;
    -o-transition: .5se ease;
    transition: .5se ease;
}

.startable button:hover {
    background: none;
    border: 2px solid #fff;
}

.thumneles {
    background-color: rgb(240, 238, 238);
    margin-top: 150px;
}

.thumneles .thum {
    border-radius: 5px;
    margin-top: 40px;
    margin-bottom: 50px;
    padding-top: 70px;
    padding-bottom: 70px;
    position: relative;
    top: -100px;
    z-index: 4;
    background-color: rgb(255, 255, 255);
    -webkit-transition: .5se ease;
    -moz-transition: .5se ease;
    -o-transition: .5se ease;
    transition: .5se ease;
}

.thumneles .thum h1 {
    margin-bottom: 20px;
    color: rgb(204, 15, 15);
}

.thumneles .thum p {
    color: rgb(151, 14, 151);
}

.thumneles .thum .i1 {
    position: absolute;
    top: -55px;
    left: 130px;
    padding: 20px 20px;
    background-color: rgb(223, 219, 219);
    border-radius: 100px;
}

.thumneles .thum a {
    position: absolute;
    bottom: -25px;
    left: 145px;
    padding: 25px 32px;
    background-color: rgb(223, 219, 219);
