.timer {
    padding: 50px;
    background-color: rgb(15, 156, 15);
}

.timer .time span {
    font-size: 75px;
    font-weight: bolder;
    color: rgb(250, 252, 250);
}

.timer .time h5 {
    font-size: large;
    color: rgb(208, 208, 221);
}

.testimonials {
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #fff;
}

.testimonials h1 {
    margin-top: 25px;
    font-weight: bold;
    margin-bottom: 25px;
}

.testimonials span {
    font-size: 25px;
    font-weight: bold;
}

.testimonials .lead {
    line-height: 2em;
    font-weight: 400;
    margin-bottom: 10px;
    border-radius: 25px;
}

.testimonials .carousel-indicators {
    position: static;
    margin-left: auto;
}

.testimonials .carousel-indicators li {
    position: relative;
    left: 60px;
    text-indent: 0;
    width: 70px;
    height: 70px;
    opacity: .2;
    overflow: hidden;
}

.testimonials .carousel-indicators li.active {
    opacity: 1;
}

.testimonials .carousel-indicators #avatar1 {
    background-image: url(./image/hie.png);
    background-repeat: round;
    border-radius: 100%;
}

.testimonials .carousel-indicators #avatar2 {
    background-image: url(./image/hicit_logo1.png);
    background-repeat: round;
    border-radius: 100%;
}

.testimonials .carousel-indicators #avatar3 {
    background-image: url(./image/ihmi_logoweb.png);
    background-repeat: round;
    border-radius: 100%;
}

.footer {
    background-color: rgb(19, 18, 18);
    color: #AAA;
    margin-top: -10px;
    padding-bottom: 20px;
    padding-top: 20px;
    background-color: rgb(19, 18, 18);
}

.footer a {
    color: #AAA;
}

.footer a:hover {
    color: #FFF;
}

.footer h3 {
    color: rgb(9, 131, 168);
    letter-spacing: 1px;
    margin-bottom: 30px;
}

.footer li {
    color: rgb(180, 179, 175);
}

.footer .sitemap .three-colums {
    overflow: hidden;
}

.footer .sitemap .three-colums li {
    width: 33.3333%;
    float: left;
    padding: 5px 0;
}

.footer .sitemap .media {
    overflow: hidden;
}

.footer .articles .media {
    float: left;
    width: 50%;
}

.footer .sitemap .social-media li img {
    float: left;
    margin-right: 5px;
    opacity: .7;
    -webkit-transition: .5se ease-in-out;
    -moz-transition: .5se ease-in-out;
    -o-transition: .5se ease-in-out;
    transition: .5se ease-in-out;
}

.footer .sitemap .social-media li img {
    width: 50px;
    height: 50px;
    border-radius: 100px;
    cursor: pointer;
}

.footer .sitemap .social-media li img:hover {
    opacity: 1;
    width: 60px;
    height: 60px;
}

.articles img {
    width: 50px;
    height: 50px;
    border-radius: 5px;
}


